<template>
<div class="kind">
  <button class="btn" @click="getkind()">点击显示最新统计</button>
  <div class="box">
    <ul v-for="shop in shopList">
     <p>{{shop}}&nbsp;&nbsp;</p>
        <li v-for="goods in foodOrderList">
          <span>{{goods.user_infor.user_name}}</span>
          <span class="o-price">{{goods.user_infor.department}}</span>
          <p>{{goods.restaurant_name}} {{goods.menu_infor.menu_name}}</p>
        </li>
    </ul>
  </div>
</div>
</template>

<script>
export default  {
  name: 'kind',
  data () {
    return {
      foodOrderList: [],
      shopList:[],
      userList:[],
      foodsList:[]
    }
  },
  created () {
    // 商家列表请求
      this.$axios.get('/v1/restaurants/list/')
     .then(reponse=>{
     console.log('商家列表',reponse.data)
       for (let i = 0; i < reponse.data.length; i++ ){
         this.shopList.push(reponse.data[i].restaurant_name)
       }
       //    订单人数请求
       this.$axios.get('/v1/food_order/list')
         .then(reponse=>{
           console.log('订单人数',reponse.data);
           this.foodsList = reponse.data
         })
         .catch(error=>{
           console.log(error);
           this.$message({
             message: '网络错误',
             type: 'warning'
           })
         })
     })
     .catch(error=>{
      console.log(error);
       this.$message({
         message: '网络错误',
         type: 'warning'
       })
     })
  },
  methods: {
    getkind () {
      const that = this
      console.log(this.userList)
    }
  }
}
</script>

<style lang="scss">
.kind {
  width:100%;
  height:100%;
  .btn {
    position: relative;
    left:-300px;
    margin: 20px 30px;
  }
  .box {
    ul {
      width:100%;
      height:auto;
      display:flex;
      flex-wrap:wrap;
      justify-content: flex-start;
        p{
          width:100%;
          color: #5e7382;
        }
        li {
          border: 1px solid #E5E9F2;
          padding: 5px;
          margin: 10px;
          background-color: #ffffff;
          cursor: pointer;
          font-size: 13px;
          p {
            margin: 6px 0;
          }
        }
      }
  }
}
</style>



